import axios from "axios";
import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import "../css/Fix.css";
import Swal from "sweetalert2";

export default function Fix({ userId }) {
  const [nickChange, setNickChange] = useState("");
  const [pwChange, setPwChange] = useState("");
  const [length, setLength] = useState(true);
  const [fixBtnAble, setFixBtnAble] = useState(false);

  function fixBtn() {
    var data = {
      nickName: nickChange,
      password: pwChange,
    };
    if (nickChange === "") {
      Swal.fire({
        title : " ",
        icon : "warning"
      })

      return;
    } else if (nickChange.length <= 8) {
      console.log("8 ");
    } else if (nickChange.length > 8) {
      Swal.fire({
        title : "2~8  ",
        text : ",   ",
        icon : "question"
      })
      return;
    }
    if (pwChange === "") {
      Swal.fire({
        title : " ",
        icon : "warning"
      })
      return;
    } else if (pwChange.length < 8) {
      Swal.fire({
        title : "8  ",
        icon : "question"
      })
      return;
    } else if (pwChange.length >= 8) {
      console.log("");
    }

    axios({
      url: "http://43.201.15.34:8050/profile",
      method: "patch",
      data: data,
      withCredentials: true,
    }).then((response) => {
      console.log("", response.data);
      window.location.replace("/");
    });
  }

  const nickConfirm = () => {
    if (nickChange === "") {
      Swal.fire({
        title : " ",
        icon : "warning"
      })
      return false;
    }
    axios({
      url: "http://43.201.15.34:8050/auth/nickNameCheck",
      method: "post",
      withCredentials: true,
      data: { nickName: nickChange },
    }).then((result) => {
      let check = /^(?=.*[a-z0-9-])[a-z0-9-]{2,8}$/;
      console.log(result.data);
      console.log("code", result.data.code);
      if (result.data.code === 400) {
        Swal.fire({
          title : "  ",
          icon : "error"
        })
        setFixBtnAble(false);
      } 
      
      else if (result.data.code === 500) {
        Swal.fire({
          title : "  ",
          icon : "error"
        })
        setFixBtnAble(false);
      } 
      else if(!check.test(nickChange)){
        Swal.fire({
          title : "2~8  ",
          text : ",   ",
          icon : "question"
        })
        setFixBtnAble(false);
      }
      else {
        Swal.fire({
          title : "  .",
          icon : "success"
          })
        setFixBtnAble(true);
      }
    });
  };

  return (
    <>
      <div className="mypage-wrapper">
        <div className="mypage-left">
          <div>
            <Link to={"/mypage"}>
              <button className="left-mypage"> </button>
            </Link>
          </div>
          <div>
            <Link to={"/fix"}>
              <button className="left-fix">  </button>
            </Link>
          </div>
        </div>
        <div className="mypage-right">
          <div className="right-title">
            <div className="my-fix">  </div>
            <hr />
          </div>
          <div className="right-list">
            <div className="fix-input">
              <form className="fix-form">
                <label htmlFor="id" className="label id">
                   :
                </label>
                <input
                  disabled
                  type="text"
                  id="id"
                  className="fixid"
                  name="id"
                  value={userId}
                ></input>
                <br />

                <label htmlFor="nickname" className="label nick">
                   :
                </label>
                <input
                  maxLength={8}
                  type="text"
                  placeholder="8  "
                  id="nickname"
                  className="fixnick"
                  name="nickname"
                  value={nickChange}
                  onChange={(e) => {
                    setNickChange(e.target.value);
                    if (e.target.value.length <= 10) {
                      setLength(true);
                    } else {
                      setLength(false);
                    }
                  }}
                ></input>

                <button
                  className="nickConfirm"
                  onClick={nickConfirm}
                  type="button"
                >
                  
                </button>

                <div className={length ? "d-none" : "color"}>
                  8  
                </div>
                <br />

                <label htmlFor="password" className="label password">
                   :
                </label>
                <input
                  type="password"
                  placeholder="  "
                  id="password"
                  className="fixpw"
                  name="password"
                  value={pwChange}
                  onChange={(e) => {
                    setPwChange(e.target.value);
                  }}
                ></input>
              </form>
            </div>
            <hr />
            <div className="right-button">
              <button
                onClick={fixBtn}
                className="fixBtn"
                disabled={!fixBtnAble}
              >
                
              </button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
