import "../css/Gaesi.css";
import { useState, useRef, useMemo, useEffect } from "react";
import React from "react";
import { TextField, Button, Typography } from "@mui/material";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";
import DeleteIcon from "@mui/icons-material/Delete";
import ErrorOutlineIcon from "@mui/icons-material/ErrorOutline";
import { Link, useParams } from "react-router-dom";
import axios from "axios";
import Swal from "sweetalert2";
const Gaesi = () => {
  const [title, Settitle] = useState(""); //  
  const [content, Setcontent] = useState(""); //  
  const quillRef = useRef();
  const { postid, name } = useParams();
  console.log(title);
  console.log(content);
  const imageHandler = () => {
    console.log("      !");

    // 1.   input type=file DOM .
    const input = document.createElement("input");
    //  
    input.setAttribute("type", "file");
    input.setAttribute("accept", "image/*");
    input.click(); //     input .
    // input    .

    // input   =  
    input.addEventListener("change", async () => {
      console.log("");
      const file = input.files[0];
      // multer    .
      const formData = new FormData();
      formData.append("files", file); // formData - 
      //  multer  .
      console.log(input.files[0]);
      console.log(quillRef);
      axios({
        url: "http://43.201.15.34:8050/post/img",
        method: "post",
        data: formData,
        withCredentials: true,
      }).then((result) => {
        console.log(result.data);
        console.log(" ,   ", result.data.url);
        const IMG_URL = result.data.url;
        //  URL img  src          
        // src base64   URL        
        //     uploads      URL .

        //     -   .
        const editor = quillRef.current.getEditor(); //   
        // 1.  root innerHTML 
        // editor root   .  img .
        //   ->    URL  ->      .
        // editor.root.innerHTML =
        //   editor.root.innerHTML + `<img src=${IMG_URL} /><br/>`; //     .

        // 2.     
        const range = editor.getSelection();
        //    
        editor.insertEmbed(range.index, "image", IMG_URL);
      });
    });
  };
  const modules = useMemo(() => {
    return {
      toolbar: {
        container: [
          ["image"],
          [{ header: [1, 2, 3, false] }],
          ["bold", "italic", "underline", "strike", "blockquote"],
        ],
        handlers: {
          //     imageHandler   .
          image: imageHandler,
        },
      },
    };
  }, []);
  //    foramts 
  const formats = [
    "header",
    "bold",
    "italic",
    "underline",
    "strike",
    "blockquote",
    "image",
  ];
  useEffect(() => {
    console.log(postid);
    axios({
      url: "http://43.201.15.34:8050/post/content",
      method: "get",
      params: { postId: postid },
      withCredentials: true,
    }).then((result) => {
      console.log("", result.data);
      Settitle(result.data.title);
      const editor = quillRef.current.getEditor(); //   
      editor.clipboard.dangerouslyPasteHTML(0, result.data.content);
    });
  }, []);
  console.log(postid);
  return (
    <>
      <div className="wrap">
        <div className="content">
          <Link style={{textDecoration:"none"}} to={"/gallery/" + name}>
            <h2 variant="h2">
                {name}
            </h2>
          </Link>
          <hr></hr>
          <div className="title_head">
            <h2> </h2>
          </div>
          <div className="warning">
            <ErrorOutlineIcon
              fontSize="10px
            "
            ></ErrorOutlineIcon>
            &nbsp;   ,  .
          </div>
          <div className="warning">
            <ErrorOutlineIcon fontSize="10px"></ErrorOutlineIcon>
            &nbsp;     
          </div>
          <hr></hr>
          <div className="title">
            <TextField
              id="filled-basic"
              value={title}
              fullWidth
              type="text"
              placeholder=" "
              onChange={(e) => {
                Settitle(e.target.value);
                console.log(title);
              }}
              variant="filled"
              label=""
              color="info"
            ></TextField>
          </div>
          <div className="main_content">
            <label></label>
            <ReactQuill
              ref={quillRef}
              placeholder=" "
              theme="snow"
              style={{ height: "650px", overflow: "scroll" }}
              onChange={Setcontent}
              value={content}
              modules={modules}
              formats={formats}
              label=""
            ></ReactQuill>
          </div>
        </div>
        <div className="modify_btn">
          <Button
            variant="contained"
            startIcon={<DeleteIcon></DeleteIcon>}
            color="error"
            id="delete"
            onClick={() => {
              console.log(postid);
              // console.log("");
              
              axios({
                url: "http://43.201.15.34:8050/post/delete",
                method: "delete",
                params: { postId: postid },
                withCredentials: true,
              }).then((response) => {
                if (response.data.code == 200) {
                  Swal.fire({
                    title : "",
                    icon : "success"
                  })
                  window.location = `/gallery/${name}`;
                }
              });
            }}
          >
            
          </Button>
          <Button
            variant="contained"
            color="success"
            id="modify"
            onClick={() => {
              let data = {
                name: name,
                postId: postid,
                title: title,
                content: quillRef.current.value,
              };
              if (title.length === 0) {
                Swal.fire({
                  title : " ",
                  icon : "warning",
              })
                return;
              } else if (content.length == "" || 0 ) {
                Swal.fire({
                  title : " ",
                  icon : "warning",
              })
                return;
              }
              axios({
                url: "http://43.201.15.34:8050/post/update",
                method: "PATCH",
                data: data,
                withCredentials: true,
              }).then((response) => {
                if (response.data.code == 200) {
                  
                  Swal.fire({
                    title : " seccess",
                    icon : "success"
                  })
                  window.location = `/post/${name}/${postid}`;
                } else {
                  Swal.fire({
                    title : "  ",
                    icon: "error"
                  })
                }
              });
            }}
          >
            
          </Button>
        </div>
      </div>
    </>
  );
};
export default Gaesi;