import axios from "axios";
import "../css/PostMade.css"
import TextField from '@mui/material/TextField';
import TextareaAutosize from '@mui/material/TextareaAutosize';
import ErrorOutlineIcon from '@mui/icons-material/ErrorOutline';
import Button from '@mui/material/Button';
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";
import { useState, useMemo,useRef } from "react"
import { useParams } from "react-router-dom";
import Swal from "sweetalert2";

function PostMade() {
    const [title,setTitle]=useState("");
    const [value, setValue] = useState(''); //     state
    const quillRef = useRef(); 
    const {name}=useParams();
    const imageHandler = () => {
        console.log('      !');
      
        // 1.   input type=file DOM .
        const input = document.createElement('input');
        //  
        input.setAttribute('type', 'file');
        input.setAttribute('accept', 'image/*');
        input.click(); //     input .
        // input    .
      
        // input   =  
        input.addEventListener('change', async () => {
          console.log('');
          const file = input.files[0];
          // multer    .
          const formData = new FormData();
          formData.append('files', file); // formData - 
          //  multer  .
          console.log(input.files[0]);
          console.log(quillRef);
          axios({
            url: "http://43.201.15.34:8050/post/img",
            method: "post",
            data: formData,
            withCredentials:true
          }).then((result) => {
            console.log(result.data);
            console.log(' ,   ', result.data.url);
            const IMG_URL = result.data.url;
            //  URL img  src          
            // src base64   URL        
            //     uploads      URL .
      
            //     -   .
            const editor = quillRef.current.getEditor(); //   
            // 1.  root innerHTML 
            // editor root   .  img .
            //   ->    URL  ->      .
            // editor.root.innerHTML =
            //   editor.root.innerHTML + `<img src=${IMG_URL} /><br/>`; //     .
      
            // 2.     
            const range = editor.getSelection();
            //    
            editor.insertEmbed(range.index, 'image', IMG_URL);
          });
          
        });
      };
    const modules = useMemo(() => {
        return {
          toolbar: {
            container: [
              ['image'],
              [{ header: [1, 2, 3, false] }],
              [{color:["orange","black"]}],
              ["size",'bold', 'italic', 'underline', 'strike', 'blockquote'],
            ],
            handlers: {
              //     imageHandler   .
              image: imageHandler,
            },
          },
        };
      }, []);
      //    foramts 
      const formats = [
        'header',
        'bold',
        'italic',
        'underline',
        'strike',
        'blockquote',
        'image',
      ];
      const sendData = async ()=>{
        let data={
          board:name,
          title:title,
          content:quillRef.current.value
        }
        if(title.length===0){
          Swal.fire({
            title : " ",
            icon : "warning",
        })
          return;
        }
        const result = await axios({
          url: "http://43.201.15.34:8050/post/uploads",
          method: "post",
          data: data,
          withCredentials:true
        })
        if(result.data.code==200){
          window.location=`/gallery/${name}`;
        }
        else{
          Swal.fire({
            title : "  ",
            icon : "error"
          })
          window.location=`/gallery/${name}`;
        }
      }
    return (
        <div className="frame">
            <div className="Logo">
                <h2> </h2>
            </div>
            <hr />
            <div className="titlename">
                <TextField onChange={(e)=>{setTitle(e.target.value)
                if(e.target.value.length >= 15){
                  Swal.fire({
                    title : "  ~!",
                    icon : "question"
                  })
                  e.target.value = ""
                }}} fullWidth type="text" placeholder="" maxLength={15}/>
            </div>
            <div className="alertstory">
                <ErrorOutlineIcon />{"      !"}
            </div>
            <div className="alertstory">
                <ErrorOutlineIcon />{"      !"}
            </div>
            <hr />
            <div className="mainPost">
                <div style={{ width: "80%" }}>
                    <ReactQuill
                        style={{height:"500px"}}
                        ref={quillRef}
                        theme="snow"
                        placeholder=" "
                        value={value}
                        onChange={setValue}
                        modules={modules}
                        formats={formats}
                    />
                </div>
                <div style={{width:"100%",marginTop:"50px",display:"flex",justifyContent:"flex-end",marginRight:"170px"}}>
                  <Button style={{background:"#4545AC"}} onClick={sendData} variant="contained">  </Button>
                </div>
            </div>
        </div>
    );
}

export default PostMade;