import React, { useState } from "react";
import axios from "axios";
import "../css/NewBoard.css";
import Button from "@mui/material/Button"
import CheckIcon from '@mui/icons-material/Check';
import ExitToAppIcon from '@mui/icons-material/ExitToApp';
import Swal from "sweetalert2";

function NewBoard(){
    const [Name, setName] = useState("");
    const [BtnAble, setBtnAble ] = useState(false);
    const [TxtLength, setTxtLength] = useState(0);
    const create = () => {
        axios({
            url: "http://43.201.15.34:8050/gallery/add",
            method: "post",
            withCredentials: true,
            data : {name : Name}
        }).then((result) => {
            if(result.data.code === 200){
                Swal.fire({
                    title : "  suckcex",
                    icon : "success",
                    // timer : 3000 
                })
                window.location.replace(`/gallery/${Name}`)
            }
            else{
                Swal.fire({
                    title: " ",
                    icon : "error"
                })
            }
        }); 
    };
    const checkBoardName = () => {
        if(Name == ""){
            Swal.fire({
                title : " ",
                icon : "warning",
            })
            return false;
        }
        axios({
            url: "http://43.201.15.34:8050/gallery/check",
            method: "get",
            withCredentials: true,
            params : {name : encodeURI(Name)}
        }).then((result) => {
            console.log("resultData : ", result.data);
            if (result.data.code === 400){
                Swal.fire({
                    title: "  ",
                    icon: "error"
                })
                setBtnAble(false);
            }
            else if (result.data.code === 500){
                Swal.fire({
                    title: "  ",
                    icon: "error"
                })
                setBtnAble(false);
            }
            else {
                Swal.fire({
                    title : "   ",
                    icon : "info"
                })
                setBtnAble(true);
            }
        })
    }
    return (
        <>
            <div className="newBoardBox">
                <div className="theContent">
                    <div className="newBoardTitle">
                        </div>
                        <div className="newBoardRule">
                            <h3>   </h3>
                            <div className="theRule">1.           .</div>
                            <div className="theRule">2.          .</div>
                            <div className="theRule">3. , , ,   ,    ,     .</div>
                            <hr></hr>
                        </div>
                    <span className="theTitle">  : </span>
                    <input  
                        className="boardName" 
                        placeholder="    " 
                        onChange={(e)=>{
                            setName(e.target.value);
                            setBtnAble(false);
                            setTxtLength(e.target.value.length);
                            if(e.target.value.length > 6){
                                Swal.fire({
                                    title: " 6  ",
                                    icon : "question"
                                })
                            }
                        }}
                        maxLength={6}
                        >
                    </input>
                    <Button 
                        startIcon={<CheckIcon></CheckIcon>}
                        className="checkBtn" 
                        onClick={()=>{
                        checkBoardName();
                    }}></Button>
                    {/* <br></br> */}
                    <div className="maxTxt">
                        <span>{TxtLength}</span>
                        /
                        <span>6</span>
                    </div>
                    <br></br>
                    <Button 
                        startIcon={<ExitToAppIcon></ExitToAppIcon>}
                        className="createBtn" 
                        onClick={()=>{
                        create();
                        }} 
                        disabled={!BtnAble}>
                     
                    </Button>
                </div>
            </div>
        </>
    );
}

export default NewBoard;